export default {
  history(){    //返回上一页
    window.history.go(-1)
  },
  isEmptyStr(str){    //检查是不是空
    if(str == null || str == undefined || str == ''){
      return true;
    }else{
      return false;
    }
  },
  arrRemove(arr, obj){
    for (var i = 0; i < arr.length; i++) {
      var temp = arr[i];
      if (!isNaN(obj)) {
        temp = i;
      }
      if (temp == obj) {
        for (var j = i; j < arr.length; j++) {
          arr[j] = arr[j + 1];
        }
        arr.length = arr.length - 1;
      }
    }
    return arr
  },

}



