<script src="../dist/router.js"></script>
<template>
  <div id="app">
    <transition name="fade" mode="in-out">
      <router-view ></router-view>
    </transition>

  </div>
</template>

<script>
  export default {
    name: 'app',
    data(){
      return{
        transitionName: 'slide-left'
      }
    }
  }
</script>

<style>
  #app,.nav,header,video{
    margin: 0 auto;
    max-width: 768px;
    overflow: auto;
  }
</style>



