
<template>
  <div class="page">
    <div class="main">

      <transition name="fade" mode="in-out">
        <router-view ></router-view>
      </transition>

    </div>
    <div class="nav flex">
      <div class="item flex1"  :class="item.path===activeName? 'focus' : '' "  v-for="(item,index) in navList" @click="handleClick(item)">
        <img v-show="item.path!=activeName" :src="item.src">
        <img  v-show="item.path==activeName" :src="item.active" >
        <p>{{item.text}}</p>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      navList:[
        {
          src:"../static/img/home/home.png",
          active:"../static/img/home/home_a.png",
          text:"首页",
          path:"/home",
        },
        {
          src:"../static/img/home/member.png",
          active:"../static/img/home/member_a.png",
          text:"会员",
          path:"/vip",
        },
        {
          src:"../static/img/home/live.png",
          active:"../static/img/home/live_a.png",
          text:"直播",
          path:"/live",
        },
        {
          src:"../static/img/home/my.png",
          active:"../static/img/home/my_a.png",
          text:"我的",
          path:"/my",
        },
      ],
      activeName: '/home'
    }
  },
  methods: {
    handleClick(item) {
      this.activeName=item.path
      this.$router.push({path:item.path});
    },
    //退出登录
    logout: function () {
      var _this = this;
      this.$confirm('确认退出吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.$store.dispatch('Logout').then(() => {
          this.$router.push({ path: '/login' });
        }).catch(err => {
          this.$message.error(err);
        });
      }).catch(() => {

      });


    },

  },
  mounted(){
    this.activeName=this.$route.path
    console.log(this.$route)
  }


}
</script>

<style scoped>
  .main{
    /*position: fixed;;top: 0;*/
    width: 100%;
    height: calc(100vh - 53px);
    overflow: auto;
  }
  .nav{
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 5px 0;
    background: #fff;
  }
  .nav img{
    width: 20px;
    height: auto;
  }
  .nav p{
    margin-top: 6px;
    font-size: 14px;
    color: #1B1B1B;
  }
  .focus p{
    color: #41E33D!important;
  }
</style>



