import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/index'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component: Index,
      meta: {role: 'admin'},
      children: [
        { path: '/',redirect:'/home', component: resolve => require(["../components/home/nav.vue"],resolve),
          children: [
            {path: '/home', name: '首页', component: resolve => require(["../components/home/home.vue"],resolve)},
            {path: '/video', name: '视频', component: resolve => require(["../components/home/video.vue"],resolve)},
            {path: '/voice', name: '语音', component: resolve => require(["../components/home/voice.vue"],resolve)},
            {path: '/friendship', name: '友情服务', component: resolve => require(["../components/home/friendship.vue"],resolve)},
            {path: '/mall', name: '商城', component: resolve => require(["../components/home/mall.vue"],resolve)},
            {path: '/applicationlecturer', name: '成为讲师', component: resolve => require(["../components/home/Applicationlecturer.vue"],resolve)},
            {path: '/activity', name: '活动专栏', component: resolve => require(["../components/home/activity.vue"],resolve)},
          ],
        },
        { path: '/vip', component: resolve => require(["../components/vip/vip.vue"],resolve), name: '会员'},
        { path: '/live',redirect:'/smallvideo', component: resolve => require(["../components/live/live.vue"],resolve), name: '直播',
          children: [
            {path: '/smallvideo', name: '以往小视频', component: resolve => require(["../components/live/smallvideo.vue"],resolve)},
            {path: '/liveing', name: '正在直播', component: resolve => require(["../components/live/liveing.vue"],resolve)},
            {path: '/livecountdown', name: '直播倒计时', component: resolve => require(["../components/live/livecountdown.vue"],resolve)},
            {path: '/onelive', name: '一对一直播', component: resolve => require(["../components/live/onelive.vue"],resolve)},
            {path: '/pastlive', name: '以往直播', component: resolve => require(["../components/live/pastlive.vue"],resolve)},
          ],
        },
        { path: '/my', component: resolve => require(["../components/my/my.vue"],resolve), name: '我的'},
      ]
    },

    {path: '/coursesall',redirect:'/selectedcourses', component: resolve => require(["../components/home/coursesall.vue"],resolve), name: '精选九大类课程',
      children: [
        {path: '/selectedcourses', name: '精选课程', component: resolve => require(["../components/home/coursesall/selectedcourses.vue"],resolve)},
        {path: '/newcourses', name: '最新上线', component: resolve => require(["../components/home/coursesall/newcourses.vue"],resolve)},
        {path: '/sooncourses', name: '即将上线', component: resolve => require(["../components/home/coursesall/sooncourses.vue"],resolve)},
      ],
    },

    {path: '/signing', component: resolve => require(["../components/home/lecturer/signing.vue"],resolve), name: '电子签约'},

    {path: '/play', component: resolve => require(["../components/play.vue"],resolve), name: '播放视频'},
    // {path: '/liveroom', component: resolve => require(["../components/liveroom.vue"],resolve), name: '直播间'},

    {path: '/teacher', component: resolve => require(["../components/home/teacher.vue"],resolve), name: '推荐老师'},

    {path: '/teacherlist', component: resolve => require(["../components/home/teacherlist.vue"],resolve), name: '老师列表'},

    {path: '/classifications', component: resolve => require(["../components/home/classifications.vue"],resolve), name: '所有分类'},
    {path: '/classroom', component: resolve => require(["../components/home/classroom.vue"],resolve), name: '文化课堂'},


    {path: '/registerlecturer', component: resolve => require(["../components/home/lecturer/register.vue"],resolve), name: '申请注册'},
    {path: '/audit', component: resolve => require(["../components/home/lecturer/audit.vue"],resolve), name: '注册审核中'},

    { path: '/rank',redirect:'/videocourse', component: resolve => require(["../components/home/rank.vue"],resolve),
      children: [
        {path: '/videocourse', name: '视频课程', component: resolve => require(["../components/rank/video.vue"],resolve)},
        {path: '/phoneticcourse', name: '语音课程', component: resolve => require(["../components/rank/voice.vue"],resolve)},
        {path: '/livecours', name: '直播课程', component: resolve => require(["../components/rank/live.vue"],resolve)},
        {path: '/hotteacher', name: '热门老师', component: resolve => require(["../components/rank/hot.vue"],resolve)},
      ],
    },

    {path: '/login', component: resolve => require(["@/components/my/login"],resolve), name: '登录'},

    {path: '/myupload',redirect:'/uploadall', component: resolve => require(["@/components/my/myupload"],resolve), name: '我的上传',
      children: [
        {path: '/uploadall', name: '全部', component: resolve => require(["../components/my/myupload/uploadall.vue"],resolve)},
        {path: '/uploadacomplete', name: '已上架', component: resolve => require(["../components/my/myupload/uploadacomplete.vue"],resolve)},
        {path: '/uploadasoon', name: '未上架', component: resolve => require(["../components/my/myupload/uploadasoon.vue"],resolve)},
        {path: '/uploadaexamine', name: '视频审核中', component: resolve => require(["../components/my/myupload/uploadaexamine.vue"],resolve)},
      ],
    },
    {path: '/collection',redirect:'/videocollection', component: resolve => require(["@/components/my/collection"],resolve), name: '我的收藏',
      children: [
        {path: '/videocollection', name: '收藏视频', component: resolve => require(["../components/my/Collection/video.vue"],resolve)},
        {path: '/voicecollection', name: '收藏语音', component: resolve => require(["../components/my/Collection/voice.vue"],resolve)},
        {path: '/livecollection', name: '收藏直播', component: resolve => require(["../components/my/Collection/live.vue"],resolve)},
      ],
    },
    {path: '/myorder',redirect:'/allorder', component: resolve => require(["@/components/my/myorder"],resolve), name: '我的订单',
      children: [
        {path: '/allorder', name: '全部订单', component: resolve => require(["../components/my/myorder/allorder.vue"],resolve)},
        {path: '/unpaidorder', name: '待付款', component: resolve => require(["../components/my/myorder/unpaidorder.vue"],resolve)},
        {path: '/appraisedorder', name: '待评价', component: resolve => require(["../components/my/myorder/appraisedorder.vue"],resolve)},
        {path: '/saleorder', name: '销售订单', component: resolve => require(["../components/my/myorder/saleorder.vue"],resolve)},
        {path: '/presaleorder', name: '预售订单', component: resolve => require(["../components/my/myorder/presaleorder.vue"],resolve)},

      ],
    },

    {path: '/bind', component: resolve => require(["@/components/my/bind"],resolve), name: '绑定手机'},
    {path: '/set', component: resolve => require(["@/components/my/set/set"],resolve), name: '设置'},
    {path: '/user', component: resolve => require(["@/components/my/set/user"],resolve), name: '个人信息'},
    {path: '/wallet', component: resolve => require(["@/components/my/set/wallet"],resolve), name: '我的钱包'},
    {path: '/recharge', component: resolve => require(["@/components/my/set/recharge"],resolve), name: '充值'},
    {path: '/forward', component: resolve => require(["@/components/my/set/forward"],resolve), name: '提现'},
    {path: '/payment', component: resolve => require(["@/components/my/set/payment"],resolve), name: '确认支付'},
    {path: '/idcorrect', component: resolve => require(["@/components/my/set/idcorrect"],resolve), name: '身份认证'},
    {path: '/phonecorrect', component: resolve => require(["@/components/my/set/phonecorrect"],resolve), name: '手机认证'},

    {path: '/evaluate', component: resolve => require(["@/components/my/evaluate"],resolve), name: '课程评价'},
    {path: '/profit', component: resolve => require(["@/components/my/profit"],resolve), name: '收益'},
    {path: '/extension', component: resolve => require(["@/components/my/extension"],resolve), name: '推广'},
    {path: '/bill', component: resolve => require(["@/components/my/bill"],resolve), name: '账单'},
    {path: '/payrecord', component: resolve => require(["@/components/my/payrecord"],resolve), name: '交易记录'},

    {path: '/news', component: resolve => require(["@/components/my/news"],resolve), name: '消息中心'},
    {path: '/newslist', component: resolve => require(["@/components/my/newslist"],resolve), name: '消息列表'},
    {path: '/newsdetails', component: resolve => require(["@/components/my/newsdetails"],resolve), name: '消息内容'},
    {path: '/dialog', component: resolve => require(["@/components/my/dialog"],resolve), name: '消息对话框'},

    {path: '/myliveroom', component: resolve => require(["@/components/my/liveroom"],resolve), name: '我的直播间'},
    {path: '/liveset', component: resolve => require(["@/components/my/liveset"],resolve), name: '直播间设置'},
    {path: '/liveestablish', component: resolve => require(["@/components/my/liveestablish"],resolve), name: '创建直播间'},
    {path: '/currestablish', component: resolve => require(["@/components/my/currestablish"],resolve), name: '创建系列课'},

    {path: '/appreciate', component: resolve => require(["@/components/my/appreciate"],resolve), name: '赞赏功能'},
    {path: '/administration', component: resolve => require(["@/components/my/administration"],resolve), name: '管理员设置'},

    {path: '/setpassword', component: resolve => require(["@/components/my/setpassword"],resolve), name: '设置支付密码'},
    {path: '/changepassword', component: resolve => require(["@/components/my/changepassword"],resolve), name: '修改支付密码'},
    {path: '/repassword', component: resolve => require(["@/components/my/repassword"],resolve), name: '重置支付密码'},

    {
      path: '/404',
      component: resolve => require(["@/components/error/404"],resolve),
      name: '',
      hidden: true
    },
    {
      path: '*',
      hidden: true,
      redirect: { path: '/404' }
    },

  ]
})



export const powerRouter =[


];



